






















import service from '../../service.js';

export default {
    data() {
        return {
            labelText: '新用户正在注册“像游”账户',
            account: '',
            password: '',
            email: ''
        }
    },
    methods: {
        register() {
            if (this.account.length < 5) {
                uni.showToast({
                    icon: 'none',
                    title: '账号最短为 5 个字符'
                });
                return;
            }
            if (this.password.length < 6) {
                uni.showToast({
                    icon: 'none',
                    title: '密码最短为 6 个字符'
                });
                return;
            }
            if (this.email.length < 3 || !~this.email.indexOf('@')) {
                uni.showToast({
                    icon: 'none',
                    title: '邮箱地址不合法'
                });
                return;
            }

            const data = {
                account: this.account,
                password: this.password,
                email: this.email
            }
            service.addUser(data);
            uni.showToast({
                title: '注册成功'
            });
            uni.navigateBack({
                delta: 1
            });
        }
    }
}



